const STRIPE_CARD_DND_ID = "stripe-mrr-card-dnd-id";
const STRIPE_MRR_PSEUDO_TECH_ID = "internal_stripe_mrr_card";
const TRASH_ID = "trash-can-droppable-id";

function DroppableTrash({ isVisible }: { isVisible: boolean }) {
  const { setNodeRef, isOver } = useDroppable({
    id: TRASH_ID,
  });

  if (!isVisible) {
    return null;
  }

  return (
    <div
      ref={setNodeRef}
      className={`mt-8 p-6 border-2 ${
        isOver
          ? "border-red-500 bg-red-500/10"
          : "border-dashed border-[var(--border)]"
      } rounded-lg flex flex-col items-center justify-center transition-all`}
      style={{ minHeight: "100px" }}
    >
      <TrashIcon
        size={32}
        className={`${
          isOver ? "text-red-500" : "text-[var(--muted-foreground)]"
        } mb-2`}
      />
      <p
        className={`text-sm ${
          isOver ? "text-red-500" : "text-[var(--muted-foreground)]"
        }`}
      >
        {isOver ? "RelÃ¢cher pour supprimer" : "Glisser ici pour supprimer"}
      </p>
    </div>
  );
}

// Interface for a complete stack (details + technologies)
export interface UserStack {
  id: number; // techStack ID
  name: string;
  technologies: Tech[];
  createdAt?: string;
  updatedAt?: string;
}

function SortableTechCard({
  tech,
  isPageEditMode,
  isResizeMode,
  onRemoveTech,
  onUpdateTech,
  gridMax,
}: {
  tech: Tech;
  isPageEditMode: boolean;
  isResizeMode: boolean;
  onRemoveTech: (id: string) => void;
  onUpdateTech: (id: string, updates: Partial<Tech>) => void;
  gridMax: { cols: number; rows: number };
}) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: tech.id });

  const style = {
    transform: transform
      ? `translate3d(${transform.x}px, ${transform.y}px, 0)`
      : undefined,
    transition,
    zIndex: isDragging ? 50 : undefined,
    opacity: isDragging ? 0.7 : 1,
  };

  const colSpan = tech.gridSpan?.cols || 1;
  const rowSpan = tech.gridSpan?.rows || 1;

  // Logique de colSpan responsive
  let colSpanClass = "col-span-1"; // Par dÃ©faut pour mobile (grille Ã  1 colonne)
  if (colSpan === 2) {
    colSpanClass = "col-span-1 md:col-span-2"; // Prend 2 colonnes sur md+, sinon 1
  } else if (colSpan === 3) {
    colSpanClass = "col-span-1 md:col-span-2 lg:col-span-3"; // Prend 3 sur lg+, 2 sur md+, sinon 1
  }
  // Pour colSpan === 1, reste col-span-1 sur toutes les tailles implicitement

  // Pour rowSpan, c'est gÃ©nÃ©ralement moins problÃ©matique, mais on peut standardiser
  const rowSpanClass = rowSpan === 2 ? "row-span-2" : "row-span-1";

  const maxCols = gridMax.cols;
  const maxRows = gridMax.rows;
  const showControls = isResizeMode;
  const gapPx = 16;
  let minHeight = rowSpan * 150 + (rowSpan - 1) * gapPx;
  // if (isResizeMode) {
  //   minHeight += 70;
  // }

  const isGithubProject =
    tech.isProject && tech.url && tech.url.startsWith("https://github.com/");
  const [isRefreshing, setIsRefreshing] = useState(false);

  const handleRefreshGithubStats = async () => {
    if (!tech.url) return;
    setIsRefreshing(true);
    try {
      const match = tech.url.match(/github.com\/([^/]+)\/([^/]+)/);
      if (!match) return;
      const owner = match[1];
      const repo = match[2];
      const res = await fetch(`https://api.github.com/repos/${owner}/${repo}`);
      if (!res.ok) throw new Error("Erreur lors du fetch GitHub");
      const data = await res.json();
      onUpdateTech(tech.id, {
        stars: data.stargazers_count,
        forks: data.forks_count,
      });
    } catch (e) {
      // GÃ©rer l'erreur
    } finally {
      setIsRefreshing(false);
    }
  };

  const CardContent = (
    <div
      className="bento-card p-4 rounded-2xl border border-[--border] flex flex-col items-start backdrop-blur-md relative overflow-hidden group transition-shadow hover:shadow-lg hover:border-[var(--primary)] h-full"
      style={{ minHeight }}
    >
      {/* Halo lumineux dÃ©coratif */}
      <div className="absolute inset-0 pointer-events-none z-0">
        <div
          className="absolute -top-8 -left-8 w-32 h-32 rounded-full blur-2xl"
          style={{
            background: `radial-gradient(circle, ${tech.color}55 0%, transparent 80%)`,
          }}
        />
      </div>
      <div className="flex items-center gap-2 mb-2 relative z-10">
        <div className="w-8 h-8 flex items-center justify-center">
          {tech.icon}
        </div>
        <span className="font-semibold text-lg text-[--text-foreground]">
          {tech.name}
        </span>
      </div>
      {/* Cacher la description si en mode Resize */}
      {tech.description && !isResizeMode && (
        <div className={`text-xs text-[--text-foreground] mt-1 relative z-10`}>
          {tech.description}
        </div>
      )}
      {/* Cacher les stats GitHub si en mode Resize */}
      {isGithubProject && !isResizeMode && (
        <div className="flex gap-4 items-center mt-2 text-xs text-[var(--muted-foreground)]">
          <span title="Stars">â­ {tech.stars ?? 0}</span>
          <span title="Forks">ðŸ´ {tech.forks ?? 0}</span>
        </div>
      )}

      {showControls && (
        <div className="mt-2 flex flex-col gap-1 w-full relative z-10">
          <div className="flex gap-1 items-center">
            <span className="text-xs">Width :</span>
            {[1, 2, 3].map((c) => (
              <Button
                key={c}
                size="sm"
                variant={colSpan === c ? "secondary" : "outline"}
                onClick={(e) => {
                  e.stopPropagation();
                  onUpdateTech(tech.id, {
                    gridSpan: { ...tech.gridSpan, cols: c as 1 | 2 | 3 },
                  });
                }}
                disabled={c > maxCols}
                className="px-2 py-0.5 text-xs h-auto min-w-[24px]"
              >
                {c}
              </Button>
            ))}
          </div>
          <div className="flex gap-1 items-center">
            <span className="text-xs">Height :</span>
            {[1, 2].map((r) => (
              <Button
                key={r}
                size="sm"
                variant={rowSpan === r ? "secondary" : "outline"}
                onClick={(e) => {
                  e.stopPropagation();
                  onUpdateTech(tech.id, {
                    gridSpan: { ...tech.gridSpan, rows: r as 1 | 2 },
                  });
                }}
                disabled={r > maxRows}
                className="px-2 py-0.5 text-xs h-auto min-w-[24px]"
              >
                {r}
              </Button>
            ))}
          </div>
        </div>
      )}
    </div>
  );

  const WrapperElement =
    isGithubProject && !isPageEditMode && !isResizeMode ? "a" : "div";

  const wrapperProps: any = {
    ref: setNodeRef,
    style: style,
    className: `relative group ${colSpanClass} ${rowSpanClass} ${
      isPageEditMode ? "cursor-grab active:cursor-grabbing" : ""
    }`,
    ...(isPageEditMode ? { ...attributes, ...listeners } : {}),
  };

  if (WrapperElement === "a") {
    wrapperProps.href = tech.url;
    wrapperProps.target = "_blank";
    wrapperProps.rel = "noopener noreferrer";
    wrapperProps["aria-label"] = `Voir le repo GitHub ${tech.name}`;
    wrapperProps.tabIndex = 0;
  }

  return <WrapperElement {...wrapperProps}>{CardContent}</WrapperElement>;
}

// AprÃ¨s la fonction SortableTechCard, ajoutons une fonction pour crÃ©er la carte Stripe

function StripeCard({
  id,
  hasStripeConnection,
  isConnectingStripe,
  handleConnectStripe,
  mrrData,
  onFetchMrr,
  isFetchingMrr,
  handleDisconnectStripe,
  isDisconnectingStripe,
  isPageEditMode,
  isResizeMode,
  onUpdateStripeCardSize,
  gridMax,
  colSpan: currentCardColSpan,
  rowSpan: currentCardRowSpan,
}: {
  id: string;
  hasStripeConnection: boolean;
  isConnectingStripe: boolean;
  handleConnectStripe: () => void;
  mrrData?: { total: number; currency: string } | null;
  onFetchMrr: () => void;
  isFetchingMrr: boolean;
  handleDisconnectStripe: () => void;
  isDisconnectingStripe: boolean;
  isPageEditMode: boolean;
  isResizeMode: boolean;
  onUpdateStripeCardSize: (updates: { cols?: 1 | 2 | 3; rows?: 1 | 2 }) => void;
  gridMax: { cols: number; rows: number };
  colSpan: 1 | 2 | 3;
  rowSpan: 1 | 2;
}) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: id, disabled: !isPageEditMode });

  const style = {
    transform: transform
      ? `translate3d(${transform.x}px, ${transform.y}px, 0)`
      : undefined,
    transition,
    opacity: isDragging ? 0.7 : 1,
  };

  // Logique de colSpan responsive
  let colSpanClass = "col-span-1";
  if (currentCardColSpan === 2) {
    colSpanClass = "col-span-1 md:col-span-2";
  } else if (currentCardColSpan === 3) {
    colSpanClass = "col-span-1 md:col-span-2 lg:col-span-3";
  }

  const rowSpanClass = currentCardRowSpan === 2 ? "row-span-2" : "row-span-1";
  const minHeight = currentCardRowSpan * 150 + (currentCardRowSpan - 1) * 16; // 16px gap
  const showResizeControls = isResizeMode && !isPageEditMode;

  return (
    <div
      ref={setNodeRef}
      style={style}
      className={`${colSpanClass} ${rowSpanClass} ${
        isPageEditMode ? "cursor-grab active:cursor-grabbing" : ""
      }`}
      {...(isPageEditMode ? { ...attributes, ...listeners } : {})}
    >
      <div
        className="bento-card p-4 rounded-2xl border border-[--border] flex flex-col items-start backdrop-blur-md relative overflow-hidden group transition-shadow hover:shadow-lg hover:border-[var(--primary)] h-full"
        style={{ minHeight }}
      >
        {/* Decorative background */}
        <div className="absolute inset-0 pointer-events-none z-0">
          <div
            className="absolute -top-8 -left-8 w-32 h-32 rounded-full blur-2xl"
            style={{
              background: `radial-gradient(circle, rgba(103, 73, 255, 0.3) 0%, transparent 80%)`,
            }}
          />
        </div>

        <div className="flex items-center gap-2 mb-4 relative z-10 w-full">
          <div className="w-8 h-8 flex items-center justify-center">
            {/* Stripe SVG Icon */}
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M13.4764 7.45119C13.4764 6.54994 14.1659 6.12994 15.2364 6.12994C16.6114 6.12994 18.3889 6.63244 19.7239 7.49994V2.93744C18.2699 2.32494 16.8464 2.06244 15.2364 2.06244C11.3764 2.06244 8.77637 4.24494 8.77637 7.67994C8.77637 13.2349 16.2464 12.141 16.2464 14.8575C16.2464 15.9975 15.3539 16.4175 14.2464 16.4175C12.7414 16.4175 10.7764 15.7485 9.30137 14.7524V19.4324C10.9364 20.1675 12.5939 20.4999 14.2464 20.4999C18.1954 20.4999 20.9989 18.3749 20.9989 14.8575C20.9989 8.89119 13.4764 10.1625 13.4764 7.45119Z"
                fill="#635BFF"
              />
            </svg>
          </div>
          <span className="font-semibold text-lg text-[--text-foreground]">
            Stripe MRR
          </span>
        </div>

        {/* Contenu principal : affichÃ© seulement si les contrÃ´les de redimensionnement ne sont PAS actifs */}
        {!showResizeControls && (
          <div className="flex-grow flex flex-col w-full overflow-y-auto">
            {!hasStripeConnection ? (
              <Button
                onClick={handleConnectStripe}
                disabled={isConnectingStripe}
                className="mt-2 w-full"
              >
                {isConnectingStripe ? (
                  <>
                    <RefreshCw size={16} className="animate-spin mr-2" />
                    Connexion...
                  </>
                ) : (
                  "Connecter avec Stripe"
                )}
              </Button>
            ) : mrrData ? (
              <div className="flex flex-col w-full">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-3xl font-bold text-[--text-foreground]">
                    {(mrrData.total / 100).toLocaleString("fr-FR", {
                      style: "currency",
                      currency: mrrData.currency.toUpperCase(),
                      minimumFractionDigits: 2,
                      maximumFractionDigits: 2,
                    })}
                  </span>
                  <span className="text-xs text-[var(--muted-foreground)]">
                    MRR Total
                  </span>
                </div>
              </div>
            ) : (
              <div className="flex flex-col items-center justify-center flex-grow w-full">
                <div className="p-3 bg-green-500/10 text-green-600 rounded-md border border-green-500/20 w-full mb-3 text-center">
                  <CheckCircle2 size={20} className="inline-block mr-2" />
                  <span>Compte Stripe connectÃ©!</span>
                </div>
              </div>
            )}
          </div>
        )}

        {showResizeControls && (
          <div className="mt-auto pt-2 flex flex-col gap-1 w-full relative z-10 border-t border-[var(--border)]">
            <div className="flex gap-1 items-center">
              <span className="text-xs">Width :</span>
              {[1, 2, 3].map((c) => (
                <Button
                  key={`stripe-col-${c}`}
                  size="sm"
                  variant={currentCardColSpan === c ? "secondary" : "outline"}
                  onClick={(e) => {
                    e.stopPropagation();
                    onUpdateStripeCardSize({ cols: c as 1 | 2 | 3 });
                  }}
                  disabled={c > gridMax.cols}
                  className="px-2 py-0.5 text-xs h-auto min-w-[24px]"
                >
                  {c}
                </Button>
              ))}
            </div>
            <div className="flex gap-1 items-center">
              <span className="text-xs">Height :</span>
              {[1, 2].map((r) => (
                <Button
                  key={`stripe-row-${r}`}
                  size="sm"
                  variant={currentCardRowSpan === r ? "secondary" : "outline"}
                  onClick={(e) => {
                    e.stopPropagation();
                    onUpdateStripeCardSize({ rows: r as 1 | 2 });
                  }}
                  disabled={r > gridMax.rows}
                  className="px-2 py-0.5 text-xs h-auto min-w-[24px]"
                >
                  {r}
                </Button>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default function Dashboard() {
  const { data: session, isPending: isLoadingSession } = useSession();
  const router = useRouter();
  const searchParams = useSearchParams();

  // Ã‰tats principaux
  const [activeStackId, setActiveStackId] = useState<number | null>(null);
  const [userProfile, setUserProfile] = useState<{
    name: string;
    description: string;
    image?: string;
    layoutConfig?: string;
  } | null>(null);
  const [isLoadingProfile, setIsLoadingProfile] = useState(true);
  const [activeStack, setActiveStack] = useState<UserStack | undefined>(
    undefined
  );
  const [userStacks, setUserStacks] = useState<UserStack[]>([]);
  const [isLoadingInitialData, setIsLoadingInitialData] = useState(true);
  const [isPageEditMode, setIsPageEditMode] = useState(false);
  const [isAddTechModalOpen, setIsAddTechModalOpen] = useState(false);
  const [isResizeMode, setIsResizeMode] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [stackToDelete, setStackToDelete] = useState<number | null>(null);
  const [itemToRemove, setItemToRemove] = useState<string | null>(null);
  const [showRemoveItemConfirm, setShowRemoveItemConfirm] = useState(false);

  // Ã‰tats pour Stripe
  const [hasStripeConnection, setHasStripeConnection] = useState(false);
  const [isCheckingStripeConnection, setIsCheckingStripeConnection] =
    useState(true);
  const [isConnectingStripe, setIsConnectingStripe] = useState(false);
  const [stripeAccountMrr, setStripeAccountMrr] = useState<{
    total: number;
    currency: string;
  } | null>(null);
  const [isLoadingStripeAccountMrr, setIsLoadingStripeAccountMrr] =
    useState(false);
  const [isDisconnectingStripe, setIsDisconnectingStripe] = useState(false);
  const [showStripeInGrid, setShowStripeInGrid] = useState(false);
  const [initialDataLoaded, setInitialDataLoaded] = useState(false);
  const [fetchedGitHubStats, setFetchedGitHubStats] = useState<
    Record<string, boolean>
  >({});
  const [isStripeSuccessToastShown, setIsStripeSuccessToastShown] =
    useState(false);
  const [stripeCardColSpan, setStripeCardColSpan] = useState<1 | 2 | 3>(2);
  const [stripeCardRowSpan, setStripeCardRowSpan] = useState<1 | 2>(1);
  const [stripeCardDbId, setStripeCardDbId] = useState<string | null>(null);
  const [gridItemOrder, setGridItemOrder] = useState<string[]>([]);

  const [userProfileDescription, setUserProfileDescription] = useState("");
  const [isEditingProfile, setIsEditingProfile] = useState(false);
  const [editingDescription, setEditingDescription] = useState("");
  const [showAddStackForm, setShowAddStackForm] = useState(false);

  const technologies = activeStack?.technologies || [];

  // --- DÃ©finitions de fonctions (useCallback quand nÃ©cessaire) ---

  const hydrateTechnologies = useCallback((rawTechs: any[]): Tech[] => {
    if (!rawTechs) return [];
    return rawTechs
      .filter((rawTech) => rawTech.technologyId !== STRIPE_MRR_PSEUDO_TECH_ID)
      .map((rawTech, index) => {
        let icon;
        let isProject = false;
        let favicon = undefined;
        let url = undefined;
        let description = undefined;
        let stars = 0;
        let forks = 0;

        if (rawTech.isProject) {
          isProject = true;
          url = rawTech.url;
          description = rawTech.description;
          favicon = rawTech.favicon;
          stars = rawTech.stars;
          forks = rawTech.forks;
          if (
            url &&
            typeof url === "string" &&
            url.startsWith("https://github.com/")
          ) {
            icon = <GitHubLogo width={24} height={24} />;
          } else if (favicon) {
            icon = (
              <img
                src={rawTech.favicon}
                alt={rawTech.name}
                width={24}
                height={24}
                style={{ width: "100%", height: "100%", objectFit: "contain" }}
              />
            );
          } else {
            icon = <ExternalLink size={24} />;
          }
        } else {
          const techIdToLookup = rawTech.technologyId
            ? rawTech.technologyId.toLowerCase()
            : "";
          icon = iconMap[techIdToLookup] || getDefaultIcon(rawTech.name);
        }
        const gridCols = parseInt(rawTech.gridCols || "1", 10);
        const gridRows = parseInt(rawTech.gridRows || "1", 10);
        const gridSpan =
          gridCols > 1 || gridRows > 1
            ? { cols: gridCols as 1 | 2 | 3, rows: gridRows as 1 | 2 }
            : undefined;

        const techItem: Tech = {
          id: String(rawTech.id),
          name: rawTech.name,
          color: rawTech.color,
          icon: icon,
          technologyId: rawTech.technologyId,
          category: rawTech.category,
          gridSpan: gridSpan,
          order: rawTech.order !== undefined ? rawTech.order : index,
          isProject: isProject,
          favicon: favicon,
          url: url,
          description: description,
          stars,
          forks,
        };
        return techItem;
      });
  }, []); // iconMap et getDefaultIcon sont stables et dÃ©finies hors du composant

  const saveActiveStack = useCallback(
    async (stackToSave: UserStack, showSuccessToast: boolean = true) => {
      if (!session?.user?.id) {
        toast.error("You must be logged in to save.");
        return null;
      }
      if (!stackToSave) {
        toast.error("No active stack to save.");
        return null;
      }

      const itemsToSave: any[] = [];
      gridItemOrder.forEach((itemId, index) => {
        if (itemId === STRIPE_CARD_DND_ID) {
          if (showStripeInGrid) {
            itemsToSave.push({
              id: stripeCardDbId,
              name: "Stripe MRR",
              color: "#635BFF",
              technologyId: STRIPE_MRR_PSEUDO_TECH_ID,
              category: "Services",
              gridCols: stripeCardColSpan,
              gridRows: stripeCardRowSpan,
              isProject: false,
              order: index,
            });
          }
        } else {
          const tech = stackToSave.technologies.find(
            (t) => String(t.id) === String(itemId)
          );
          if (tech) {
            itemsToSave.push({
              id: tech.id,
              name: tech.name,
              color: tech.color,
              technologyId: tech.technologyId || tech.id,
              category: tech.category,
              gridCols: tech.gridSpan?.cols || 1,
              gridRows: tech.gridSpan?.rows || 1,
              isProject: tech.isProject,
              favicon: tech.favicon,
              url: tech.url,
              description: tech.description,
              order: index,
              stars: tech.stars,
              forks: tech.forks,
            });
          }
        }
      });

      try {
        const response = await fetch("/api/tech/stack", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            id: stackToSave.id,
            name: stackToSave.name,
            technologies: itemsToSave,
          }),
        });
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || "Error while saving the stack");
        }
        const savedStackData = await response.json();
        const stripeItemFromApi = (savedStackData.technologies || []).find(
          (t: any) => t.technologyId === STRIPE_MRR_PSEUDO_TECH_ID
        );
        const regularTechsFromApi = (savedStackData.technologies || []).filter(
          (t: any) => t.technologyId !== STRIPE_MRR_PSEUDO_TECH_ID
        );

        if (stripeItemFromApi) {
          setShowStripeInGrid(true);
          setStripeCardDbId(String(stripeItemFromApi.id));
          setStripeCardColSpan((stripeItemFromApi.gridCols || 2) as 1 | 2 | 3);
          setStripeCardRowSpan((stripeItemFromApi.gridRows || 1) as 1 | 2);
        } else {
          setShowStripeInGrid(false);
          setStripeCardDbId(null);
        }
        const hydratedTechs = hydrateTechnologies(regularTechsFromApi);
        const finalStack = { ...savedStackData, technologies: hydratedTechs };
        setUserStacks((prevStacks) =>
          prevStacks.map((s) => (s.id === finalStack.id ? finalStack : s))
        );
        if (activeStackId === null && finalStack.id)
          setActiveStackId(finalStack.id);
        if (showSuccessToast)
          toast.success(`Stack '${finalStack.name}' saved successfully!`);
        return finalStack;
      } catch (error) {
        toast.error((error as Error).message || "Unable to save the stack.");
        return null;
      }
    },
    [
      session,
      activeStackId,
      hydrateTechnologies,
      showStripeInGrid,
      stripeCardDbId,
      stripeCardColSpan,
      stripeCardRowSpan,
      gridItemOrder,
      setUserStacks,
      setActiveStackId,
    ]
  );

  const handleConnectStripe = async () => {
    setIsConnectingStripe(true);
    window.location.href = "/api/stripe/oauth/authorize";
  };

  const handleShowStripeCardInGrid = useCallback(() => {
    if (!hasStripeConnection) {
      handleConnectStripe();
      return;
    }
    setShowStripeInGrid(true);
    setGridItemOrder((prevOrder) =>
      prevOrder.includes(STRIPE_CARD_DND_ID)
        ? prevOrder
        : [
            STRIPE_CARD_DND_ID,
            ...prevOrder.filter((id) => id !== STRIPE_CARD_DND_ID),
          ]
    );
    if (activeStack) {
      saveActiveStack(
        { ...activeStack, technologies: [...activeStack.technologies] },
        false
      );
    }
  }, [hasStripeConnection, activeStack, saveActiveStack]);

  const handleFetchStripeAccountMrr = useCallback(async () => {
    setIsLoadingStripeAccountMrr(true);
    try {
      const response = await fetch("/api/stripe/mrr");
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || "Failed to fetch MRR");
      }
      const data = await response.json();
      setStripeAccountMrr(data);
      toast.success("Stripe MRR data fetched successfully!");
    } catch (error: any) {
      toast.error(error.message || "Could not fetch Stripe MRR.");
      setStripeAccountMrr(null);
    } finally {
      setIsLoadingStripeAccountMrr(false);
    }
  }, []);

  const handleDisconnectStripe = useCallback(async () => {
    setIsDisconnectingStripe(true);
    try {
      const response = await fetch("/api/stripe/disconnect", {
        method: "DELETE",
      });
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || "Failed to disconnect Stripe");
      }
      toast.success("Stripe account disconnected successfully.");
      setHasStripeConnection(false);
      setStripeAccountMrr(null);
      setShowStripeInGrid(false);
      setStripeCardDbId(null);
      setGridItemOrder((prevOrder) =>
        prevOrder.filter((id) => id !== STRIPE_CARD_DND_ID)
      );
      if (activeStack && stripeCardDbId) {
        saveActiveStack(
          { ...activeStack, technologies: [...activeStack.technologies] },
          false
        );
      }
    } catch (error: any) {
      toast.error(error.message || "Could not disconnect Stripe account.");
    } finally {
      setIsDisconnectingStripe(false);
    }
  }, [activeStack, saveActiveStack, stripeCardDbId]);

  const handleUpdateStripeCardSize = useCallback(
    (updates: { cols?: 1 | 2 | 3; rows?: 1 | 2 }) => {
      let changed = false;
      if (updates.cols && stripeCardColSpan !== updates.cols) {
        setStripeCardColSpan(updates.cols);
        changed = true;
      }
      if (updates.rows && stripeCardRowSpan !== updates.rows) {
        setStripeCardRowSpan(updates.rows);
        changed = true;
      }
      if (changed && activeStack && showStripeInGrid) {
        saveActiveStack(
          { ...activeStack, technologies: [...activeStack.technologies] },
          false
        );
      }
    },
    [
      activeStack,
      showStripeInGrid,
      stripeCardColSpan,
      stripeCardRowSpan,
      saveActiveStack,
    ]
  );

  const handleUpdateTech = useCallback(
    (id: string, updates: Partial<Tech>) => {
      if (!activeStack) return;
      const updatedTechnologies = activeStack.technologies.map((tech) =>
        tech.id === id ? { ...tech, ...updates } : tech
      );
      const updatedStack = {
        ...activeStack,
        technologies: updatedTechnologies,
      };
      setUserStacks((prevStacks) =>
        prevStacks.map((s) => (s.id === activeStackId ? updatedStack : s))
      );
      if (
        typeof updates.stars !== "undefined" ||
        typeof updates.forks !== "undefined" ||
        typeof updates.description !== "undefined" ||
        typeof updates.gridSpan !== "undefined"
      ) {
        saveActiveStack(updatedStack, false);
      }
    },
    [activeStack, activeStackId, saveActiveStack, setUserStacks]
  );

  const handleRemoveTech = useCallback(
    async (stackTechnologyItemId: string) => {
      if (!activeStack) return;
      if (stackTechnologyItemId === STRIPE_CARD_DND_ID) {
        handleDisconnectStripe();
        return;
      }
      try {
        const response = await fetch(
          `/api/tech/stack?id=${stackTechnologyItemId}`,
          { method: "DELETE" }
        );
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || "Error while deleting");
        }
        const updatedTechnologies = activeStack.technologies.filter(
          (tech) => tech.id !== stackTechnologyItemId
        );
        const updatedStack = {
          ...activeStack,
          technologies: updatedTechnologies,
        };
        setUserStacks((prevStacks) =>
          prevStacks.map((s) => (s.id === activeStackId ? updatedStack : s))
        );
        setGridItemOrder((prev) =>
          prev.filter((id) => id !== stackTechnologyItemId)
        );
        toast.success("Technology removed from the active stack!");
      } catch (error) {
        toast.error(
          (error as Error).message || "Unable to delete the technology."
        );
      }
    },
    [activeStack, activeStackId, setUserStacks, handleDisconnectStripe]
  );

  // --- useEffects --- (Doivent venir APRES les dÃ©finitions de fonctions qu'ils utilisent)

  useEffect(() => {
    if (activeStackId !== null && userStacks.length > 0) {
      const currentActiveStack = userStacks.find(
        (stack) => stack.id === activeStackId
      );
      setActiveStack(currentActiveStack);
    } else if (userStacks.length === 0) {
      setActiveStack(undefined);
    }
  }, [activeStackId, userStacks]);

  useEffect(() => {
    const stripeSuccess = searchParams.get("stripe_success");
    const stripeError = searchParams.get("stripe_error");
    let newUrl = "/dashboard";

    if (stripeSuccess && !isStripeSuccessToastShown) {
      toast.success("Stripe account connected successfully!");
      setHasStripeConnection(true);
      handleShowStripeCardInGrid();
      setIsStripeSuccessToastShown(true);
      const params = new URLSearchParams(searchParams.toString());
      params.delete("stripe_success");
      newUrl = `/dashboard?${params.toString()}`;
    }
    if (stripeError) {
      toast.error(`Stripe connection failed: ${stripeError}`);
      setHasStripeConnection(false);
      setShowStripeInGrid(false);
      const params = new URLSearchParams(searchParams.toString());
      params.delete("stripe_error");
      newUrl = `/dashboard?${params.toString()}`;
    }
    if (window.location.pathname + window.location.search !== newUrl) {
      router.replace(newUrl, { scroll: false });
    }
    const checkStripeStatus = async () => {
      // ... (checkStripeStatus logic)
    };
    if (!isLoadingSession && !stripeSuccess && !stripeError)
      checkStripeStatus();
  }, [
    searchParams,
    router,
    session,
    isLoadingSession,
    isStripeSuccessToastShown,
    handleShowStripeCardInGrid,
  ]);

  useEffect(() => {
    if (activeStack) {
      const techIds = activeStack.technologies.map((t) => String(t.id));
      let currentOrderFromStack = activeStack.technologies
        .slice()
        .sort((a, b) => (a.order || 0) - (b.order || 0))
        .map((t) => String(t.id));

      let newOrder = [...currentOrderFromStack];
      const stripeItemInDb = activeStack.technologies.find(
        (t) => t.technologyId === STRIPE_MRR_PSEUDO_TECH_ID
      );

      if (showStripeInGrid) {
        if (!newOrder.includes(STRIPE_CARD_DND_ID)) {
          if (stripeItemInDb && typeof stripeItemInDb.order === "number") {
            newOrder.splice(stripeItemInDb.order, 0, STRIPE_CARD_DND_ID);
          } else {
            newOrder.unshift(STRIPE_CARD_DND_ID);
          }
        }
      } else {
        newOrder = newOrder.filter((id) => id !== STRIPE_CARD_DND_ID);
      }

      techIds.forEach((tid) => {
        if (!newOrder.includes(tid)) {
          const stripeIdx = newOrder.indexOf(STRIPE_CARD_DND_ID);
          if (stripeIdx !== -1 && stripeIdx < newOrder.length - 1) {
            newOrder.splice(stripeIdx + 1, 0, tid);
          } else {
            newOrder.push(tid);
          }
        }
      });
      newOrder = [...new Set(newOrder)];

      if (JSON.stringify(newOrder) !== JSON.stringify(gridItemOrder)) {
        setGridItemOrder(newOrder);
      }
    } else {
      setGridItemOrder(showStripeInGrid ? [STRIPE_CARD_DND_ID] : []);
    }
  }, [activeStack, showStripeInGrid]);

  useEffect(() => {
    if (session?.user) {
      const currentDesc = (session.user as any).description || "";
      setUserProfileDescription(currentDesc);
      setEditingDescription(currentDesc);
    }
  }, [session]);

  useEffect(() => {
    if (
      showStripeInGrid &&
      hasStripeConnection &&
      !stripeAccountMrr &&
      !isLoadingStripeAccountMrr
    ) {
      handleFetchStripeAccountMrr();
    }
  }, [
    showStripeInGrid,
    hasStripeConnection,
    stripeAccountMrr,
    isLoadingStripeAccountMrr,
    handleFetchStripeAccountMrr,
  ]);

  useEffect(() => {
    if (
      initialDataLoaded &&
      activeStack &&
      activeStack.technologies.length > 0
    ) {
      activeStack.technologies.forEach((tech) => {
        if (
          tech.isProject &&
          tech.url &&
          tech.url.startsWith("https://github.com/") &&
          !fetchedGitHubStats[tech.id]
        ) {
          const fetchStats = async () => {
            // ... (logique de fetchStats appelant handleUpdateTech)
          };
          fetchStats();
        }
      });
    }
  }, [initialDataLoaded, activeStack, fetchedGitHubStats, handleUpdateTech]);

  useEffect(() => {
    const fetchUserStacks = async () => {
      if (session?.user?.id) {
        setIsLoadingInitialData(true);
        try {
          const response = await fetch("/api/tech/stack");
          if (!response.ok) throw new Error("Network or server error");
          let stacksData: UserStack[] = await response.json();
          const hydratedStacks = stacksData.map((stack) => {
            const stripeItemInStack = (stack.technologies || []).find(
              (tech: any) => tech.technologyId === STRIPE_MRR_PSEUDO_TECH_ID
            );
            const regularTechs = (stack.technologies || []).filter(
              (tech: any) => tech.technologyId !== STRIPE_MRR_PSEUDO_TECH_ID
            );
            if (
              stripeItemInStack &&
              ((activeStackId === null && stack.id === stacksData[0]?.id) ||
                stack.id === activeStackId)
            ) {
              setShowStripeInGrid(true);
              setStripeCardDbId(String(stripeItemInStack.id));
              setStripeCardColSpan(
                ((stripeItemInStack as any).gridCols || 2) as 1 | 2 | 3
              );
              setStripeCardRowSpan(
                ((stripeItemInStack as any).gridRows || 1) as 1 | 2
              );
            } else if (
              !stripeItemInStack &&
              ((activeStackId === null && stack.id === stacksData[0]?.id) ||
                stack.id === activeStackId)
            ) {
              setShowStripeInGrid(false);
              setStripeCardDbId(null);
            }
            return {
              ...stack,
              technologies: hydrateTechnologies(regularTechs),
            };
          });
          setUserStacks(hydratedStacks);
          if (activeStackId === null && hydratedStacks.length > 0) {
            setActiveStackId(hydratedStacks[0].id);
          } else if (hydratedStacks.length === 0) {
            setActiveStackId(null);
          }
          setInitialDataLoaded(true);
        } catch (error) {
          toast.error("Unable to load your stacks.");
          setUserStacks([]);
          setActiveStackId(null);
        } finally {
          setIsLoadingInitialData(false);
        }
      } else if (!isLoadingSession && !session) {
        setIsLoadingInitialData(false);
        setUserStacks([]);
        setActiveStackId(null);
      }
    };
    if (!isLoadingSession) fetchUserStacks();
  }, [session, isLoadingSession, hydrateTechnologies, activeStackId]);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, { coordinateGetter: sortableKeyboardCoordinates })
  );
  const handleDragEnd = useCallback(
    (event: DragEndEvent) => {
      const { active, over } = event;
      if (over && over.id === TRASH_ID) {
        const activeId = active.id as string;
        if (activeId === STRIPE_CARD_DND_ID) {
          handleDisconnectStripe();
        } else {
          handleRemoveTech(activeId);
        }
        return;
      }
      if (over && active.id !== over.id) {
        const oldIndex = gridItemOrder.indexOf(active.id as string);
        const newIndex = gridItemOrder.indexOf(over.id as string);
        if (oldIndex !== -1 && newIndex !== -1) {
          const newOrderedGridItems = arrayMove(
            gridItemOrder,
            oldIndex,
            newIndex
          );
          setGridItemOrder(newOrderedGridItems);
          if (activeStack) {
            saveActiveStack(
              { ...activeStack, technologies: [...activeStack.technologies] },
              false
            );
          }
        }
      }
    },
    [
      gridItemOrder,
      activeStack,
      handleDisconnectStripe,
      handleRemoveTech,
      saveActiveStack,
    ]
  );

  const handleAddTech = useCallback(
    async (newTechFromForm: Tech) => {
      if (!activeStack) {
        toast.error("Please select a stack to add a technology.");
        return;
      }
      const updatedTechnologies = [
        ...activeStack.technologies,
        newTechFromForm,
      ];
      const updatedStack = {
        ...activeStack,
        technologies: updatedTechnologies,
      };
      const savedStack = await saveActiveStack(updatedStack);
      if (savedStack) {
        setUserStacks((prevStacks) =>
          prevStacks.map((s) =>
            s.id === savedStack.id ? { ...savedStack } : s
          )
        );
      }
    },
    [activeStack, saveActiveStack, setUserStacks]
  );

  const handleReorderTechs = useCallback(
    (reorderedTechs: Tech[]) => {
      if (!activeStack) return;
      const updatedStack = { ...activeStack, technologies: reorderedTechs };
      setUserStacks((prevStacks) =>
        prevStacks.map((s) => (s.id === activeStackId ? updatedStack : s))
      );
      saveActiveStack(updatedStack, false);
    },
    [activeStack, activeStackId, saveActiveStack, setUserStacks]
  );

  const handleActiveStackNameChange = useCallback(
    (newName: string) => {
      if (!activeStack || activeStack.name === newName) return;
      const updatedStack = { ...activeStack, name: newName };
      setUserStacks((prevStacks) =>
        prevStacks.map((s) => (s.id === activeStackId ? updatedStack : s))
      );
      saveActiveStack(updatedStack, false);
    },
    [activeStack, activeStackId, saveActiveStack]
  );

  const handleProfileDescriptionChange = useCallback(
    async (newDescription: string) => {
      setUserProfileDescription(newDescription);
      setIsEditingProfile(false);
      if (!session?.user?.id) {
        toast.error("You must be logged in to save your description.");
        return;
      }
      try {
        const response = await fetch("/api/user/profile", {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ description: newDescription }),
        });
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(
            errorData.error || "Error while saving the description."
          );
        }
        toast.success("Profile description saved!");
      } catch (error) {
        toast.error(
          (error as Error).message || "Unable to save the description."
        );
      }
    },
    [session?.user?.id]
  );

  const handleCreateNewStack = useCallback(
    async (stackName: string) => {
      if (!session?.user?.id) {
        toast.error("You must be logged in to create a stack.");
        return;
      }
      if (userStacks.length >= 5) {
        toast.error("You cannot have more than 5 stacks.");
        return;
      }
      try {
        const response = await fetch("/api/tech/stack", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ name: stackName, technologies: [] }),
        });
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || "Error while creating the stack");
        }
        const newStackData = await response.json();
        const hydratedNewStack: UserStack = {
          ...newStackData,
          technologies: hydrateTechnologies(newStackData.technologies || []),
        };
        setUserStacks((prevStacks) => [...prevStacks, hydratedNewStack]);
        setActiveStackId(hydratedNewStack.id);
        toast.success(`Stack '${hydratedNewStack.name}' created successfully!`);
      } catch (error) {
        toast.error((error as Error).message || "Unable to create the stack.");
      }
    },
    [session?.user?.id, userStacks.length, hydrateTechnologies]
  );

  const handleDeleteStack = useCallback(
    async (stackIdToDelete: number) => {
      if (!session?.user?.id) {
        toast.error("You must be logged in to delete a stack.");
        return;
      }
      setStackToDelete(stackIdToDelete);
      setIsDeleteDialogOpen(true);
    },
    [session?.user?.id]
  );

  const confirmDeleteStack = useCallback(async () => {
    if (stackToDelete === null) return;
    try {
      const response = await fetch(
        `/api/tech/stack?id=${stackToDelete}&stackId=true`,
        { method: "DELETE" }
      );
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || "Error while deleting the stack");
      }
      const updatedStacks = userStacks.filter(
        (stack) => stack.id !== stackToDelete
      );
      setUserStacks(updatedStacks);
      if (activeStackId === stackToDelete)
        setActiveStackId(updatedStacks.length > 0 ? updatedStacks[0].id : null);
      toast.success("Stack deleted successfully!");
    } catch (error) {
      toast.error((error as Error).message || "Unable to delete the stack.");
    } finally {
      setIsDeleteDialogOpen(false);
      setStackToDelete(null);
    }
  }, [stackToDelete, userStacks, activeStackId]);

  // ... (rest of the component code remains unchanged)

  // Le return du composant Dashboard
  if (isLoadingSession || isLoadingInitialData) {
    return <LoadingSkeleton />;
  }

  if (!session?.user?.id) {
    return (
      <div className="flex flex-col justify-center items-center min-h-screen bg-[var(--background)] text-[var(--foreground)]">
        <p className="text-xl mb-4">Please sign in to access your dashboard.</p>
        <Button
          onClick={() => (window.location.href = "/auth/signin")}
          className="bg-[var(--primary)] text-[var(--primary-foreground)] hover:bg-[var(--primary)]/90"
        >
          Sign in
        </Button>
      </div>
    );
  }

  return (
    <>
      <div className="flex flex-col min-h-screen bg-[var(--background)] text-[var(--foreground)] mx-auto max-w-4xl">
        <div className="flex-grow">
          <div className="mx-auto py-2 md:py-8 px-4 md:px-0">
            <ProfileHeader />
            <StacksSelector />
            <div className="mt-8">
              <BentoGridSection
                activeStack={activeStack}
                technologies={technologies}
                isLoadingInitialData={isLoadingInitialData}
                handleAddTech={handleAddTech}
                sessionUserId={session?.user?.id}
                handleRemoveTech={handleRemoveTech}
                handleUpdateTech={handleUpdateTech}
                hasStripeConnection={hasStripeConnection}
                isConnectingStripe={isConnectingStripe}
                handleConnectStripe={handleConnectStripe}
                stripeAccountMrr={stripeAccountMrr}
                isLoadingStripeAccountMrr={isLoadingStripeAccountMrr}
                handleFetchStripeAccountMrr={handleFetchStripeAccountMrr}
                isDisconnectingStripe={isDisconnectingStripe}
                handleDisconnectStripe={handleDisconnectStripe}
                showStripeInGrid={showStripeInGrid}
                stripeCardColSpan={stripeCardColSpan}
                stripeCardRowSpan={stripeCardRowSpan}
                handleUpdateStripeCardSize={handleUpdateStripeCardSize}
              />
            </div>
          </div>
        </div>
        <AlertDialog
          open={isDeleteDialogOpen}
          onOpenChange={setIsDeleteDialogOpen}
        >
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Are you absolutely sure?</AlertDialogTitle>
              <AlertDialogDescription>
                This action cannot be undone. This will permanently delete your
                stack and remove your data from our servers.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel onClick={() => setIsDeleteDialogOpen(false)}>
                Cancel
              </AlertDialogCancel>
              <AlertDialogAction onClick={confirmDeleteStack}>
                Continue
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      </div>
    </>
  );
}
